.item {
  height: 35px;
  width: 200px;
  background-color: rgb(0, 195, 255);
  color: rgb(255, 255, 255);
  text-transform: uppercase;
}

.string {
  text-align: center;
  height: 35px;
  color: #616161;
  text-transform: capitalize;
}

.string:nth-child(2n + 1) {
  background-color: rgb(212, 212, 212);
}
table {
  margin-top: 50px;
}
